const hebrew: { [key: string]: string } = {
  deleteTask: "",
  confirmTaskDelete: " / / /  ?",

  newTask: " ",
  moveAll: "  ",
  confirmMoveAll:
    " / / /    ?\n     ",

  taskListName_main: "",
  taskListName_0: "",
  taskListName_1: "",
  taskListName_2: "",
  taskListName_3: "",
  taskListName_4: "",
  taskListName_5: "",
  taskListName_6: "",
};

const getTranslation = (key: string) => {
  if (!(key in hebrew)) {
    console.error(`error '${key}' doesnt exist in translation map`);
    return key;
  }

  return hebrew[key];
};

export default getTranslation;
